body {
  margin: 0;
  font-family: Arial;
}

main {
  margin-top: 56px;
  height: calc(100vh - 50px);
  background: #969696;
  background: -moz-linear-gradient(top, #A6A6A6 0%, #FFFFFF 1%);
  background: -webkit-linear-gradient(top, #A6A6A6 0%, #FFFFFF 1%);
  background: linear-gradient(to bottom, #A6A6A6 0%, #FFFFFF 1%);
  padding: 2.5rem;
}
